import React from "react";

import {
  ComponentContainer,
  TriangleUp,
  ComponentSubcontainer,
  Title,
  CheckboxInput,
  LanguageTag,
} from "./dropdown-languages-card.styles";

const LanguageCard = () => {
  return (
    <ComponentContainer>
      <TriangleUp></TriangleUp>
      <ComponentSubcontainer>
        <Title>Change Language</Title>
        <div>
          <div>
            <CheckboxInput type="checkbox" />
            <LanguageTag>English - EN</LanguageTag>
            <hr />
          </div>

          <div>
            <CheckboxInput type="checkbox" />
            <LanguageTag>Espaol - ES</LanguageTag>
          </div>

          <div>
            <CheckboxInput type="checkbox" />
            <LanguageTag> - ZH </LanguageTag>
          </div>

          <div>
            <CheckboxInput type="checkbox" />
            <LanguageTag>Deutsch - DE </LanguageTag>
          </div>

          <div>
            <CheckboxInput type="checkbox" />
            <LanguageTag>Portugus - PT</LanguageTag>
          </div>

          <div>
            <CheckboxInput type="checkbox" />
            <LanguageTag> - ZH/span/</LanguageTag>
          </div>

          <div>
            <CheckboxInput type="checkbox" />
            <LanguageTag> - KO</LanguageTag>
          </div>

          <div>
            <CheckboxInput type="checkbox" />
            <LanguageTag> - HE</LanguageTag>
          </div>

          <div>
            <CheckboxInput type="checkbox" />
            <LanguageTag>  - AR </LanguageTag>
          </div>
        </div>
      </ComponentSubcontainer>
    </ComponentContainer>
  );
};

export default LanguageCard;
